.question-choice-edit {

}

.question-choice-edit input[type="text"] {
  display: inline-block;

  width: 90%;
  outline: none;

  background: #4D3675;
  border: 1px solid #1E142F;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 1.2em;

  padding: 0.5em;
  padding-bottom: 1em;
  padding-top: 1em;

  color: #C2B0E2;

  padding-left: 1em;
}


.question-choice-edit input[type="text"]::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #C2B0E2;
    opacity: 1; /* Firefox */
}


.question-choice-edit input[type="text"]:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #C2B0E2;
    opacity: 1; /* Firefox */
}


.question-choice-edit input[type="text"]::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #C2B0E2;
    opacity: 1; /* Firefox */
}


.question-choice-edit input[type="text"]::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #C2B0E2;
    opacity: 1; /* Firefox */
}

.question-choice-edit input[type="text"]:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #C2B0E2;
}

.question-choice-edit input[type="text"]::-ms-input-placeholder { /* Microsoft Edge */
    color: #C2B0E2;
}

.question-choice-edit .checkbox {
  display: inline-block;
  width: 10%;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}

.question-choice-edit .checkbox .checkmark{
  width: 10%;

  padding-top: 1.6em;
  padding-bottom: 1.3em;

  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}

.question-choice-edit input[type="text"]:focus {
  background-color: white;
  color: #847799;
}



