.question-text-edit {
  width: 100%;

  background: #FFFFFF;
  border: 1px solid #3B1168;

  border-radius: 6px;

  font-family: 'Roboto';
  font-weight: 900;
  font-size: 1.5em;
  padding: 0.5em;
  padding-bottom: 0.75em;
  padding-top: 0.75em;
  color: #847799;

  padding-left: 1em;
}

.question-text-edit input[type="text"]::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #847799;
    opacity: 1; /* Firefox */
}

.question-text-edit input[type="text"]:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #847799;
    opacity: 1; /* Firefox */
}

.question-text-edit input[type="text"]::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #847799;
    opacity: 1; /* Firefox */
}

.question-text-edit input[type="text"]::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #847799;
    opacity: 1; /* Firefox */
}

.question-text-edit input[type="text"]:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #847799;
}

.question-text-edit input[type="text"]::-ms-input-placeholder { /* Microsoft Edge */
    color: #847799;
}



